import "../components/Heroimgstyle2.css"

import React from 'react'

export default function Heroimg2(props) {
  return (
    <div>
    return (
      <div className="hero-img">
      <div className="heading">
         <h1>{props.heading}</h1>
         <p>{props.text}</p>
      </div>
      </div>
    );
    </div>
  )
};
