import React from 'react'
import "./Footerstyle.css"
import PlaceIcon from '@mui/icons-material/Place';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import MarkEmailReadIcon from '@mui/icons-material/MarkEmailRead';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import InstagramIcon from '@mui/icons-material/Instagram';
import GitHubIcon from '@mui/icons-material/GitHub';

const Footer = () => {
  return (
    <div className='footer'>
      <div className='footer-container'>
         <div className='left'>
            <div className='location'>
                <PlaceIcon size={20} style={{color:"#fff",marginRight:"2rem"}}/>
                <div>
                    <p>Ameerpet, Hyderabad</p>
                </div>
            </div>

            <div className='phone'>
                <PhoneInTalkIcon size={20} style={{color:"#fff",marginRight:"2rem"}}/>
                <div>
                    <p>+91-8093482905</p>
                 </div>
                
            </div>

            <div className='email'>
                <MarkEmailReadIcon size={20} style={{color:"#fff",marginRight:"2rem"}}/>
                <div>
                    <p>vkpandit@gmail.com</p>
                </div>
            </div>
         </div>
         <div className='right'>
          <h3 style={{color:"white"}}>About the Portfolio</h3>
          <p>This is Mr. Vikash portfolio's that defined as a collection of projects and some personal data that are held by an individual</p>
          <div className='social'>
            <FacebookIcon size={20} style={{color:"#fff",marginRight:"1.2rem"}}/>
            <TwitterIcon size={20} style={{color:"#fff",marginRight:"1.2rem"}}/>
            <LinkedInIcon size={20} style={{color:"#fff",marginRight:"1.2rem"}}/>
            <InstagramIcon size={20} style={{color:"#fff",marginRight:"1.2rem"}}/>
            <GitHubIcon size={20} style={{color:"#fff"}}/>
          </div>
         </div>
      </div>
    </div>
  )
}

export default Footer
